<template>
  <div >
    <div role="tablist">
      <b-card no-body class="mb-1">
        <!-- <div slot="header">
          <strong>Timeline</strong>
        </div> -->
        <b-card-header header-tag="header" class="p-1" role="tab">
          <b-btn block href="#" v-b-toggle.divFoodMenu variant="warning">Food Menu</b-btn>
        </b-card-header>

        <b-collapse id="divFoodMenu" accordion="db-accordion" role="tabpanel">
          <food-menu></food-menu>
        </b-collapse>
      </b-card>
      <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
          <b-btn block href="#" v-b-toggle.divFoodMenu2 variant="info">Food Menu 2</b-btn>
        </b-card-header>

        <b-collapse id="divFoodMenu2" accordion="db-accordion" role="tabpanel">
          <food-menu></food-menu>
        </b-collapse>
      </b-card>
    </div>
  </div>
</template>

<script>

import FoodMenu from '@/views/FoodMenu'

let postsRef = firebase.database().ref('posts');

export default {
  name: 'timeline',
  components: { FoodMenu },
  data() {
    return {
      allPosts: [],
      allFields: []
    }
  },
  created: function() {
    postsRef.on('child_added', snapshot => {
      this.allPosts.push(snapshot.val());
      this.allFields.push(Object.keys(this.allPosts[0].values[0]));
    });
    

  },
  methods: {

  }
}
</script>

<style>
  /* IE fix */
  #card-chart-01, #card-chart-02 {
    width: 100% !important;
  }
</style>



