<template>
  <b-card-body>
    <div slot="header">
      <strong>Timeline</strong>
    </div>
    <div v-for="(eachPost, postIndex) in allPosts" v-bind:key="postIndex">
      <!-- <div class="h4 m-0 text-center padding-bottom-10">{{ eachPost.category }}</div> -->
      <!-- <b-table :hover="true" :striped="true" :bordered="true" :small="true" :fixed="true" responsive="lg" :items="eachPost.values" :fields="allFields">
      </b-table> -->
  <div v-for="(everyDay, dayIndex) in eachPost.values" v-bind:key="dayIndex">
      <div v-if="everyDay.Option === 'Lunch'">
        <strong>Lunch</strong>
        <span v-for="(eachHeader, headerIndex) in allFields" v-bind:key="headerIndex">
          <span v-if="everyDay[eachHeader] !== 'Lunch'">
            <code>{{eachHeader}}</code>
            <span>{{ everyDay[eachHeader] }}</span>
          </span>
        </span>
        <br>
      </div>
      <div v-if="everyDay.Option === 'Dinner'">
        <strong>Dinner</strong>
        <span v-for="(eachHeader, headerIndex) in allFields" v-bind:key="headerIndex">
          <span v-if="everyDay[eachHeader] !== 'Dinner'">
            <code>{{eachHeader}}</code>
            <span>{{ everyDay[eachHeader] }}</span>
          </span>
        </span>
        <br>
      </div>
  </div>
    </div>

    <!-- <b-row>
      <b-col sm="12" lg="12" v-for="(eachPost, postIndex) in allPosts" v-bind:key="postIndex">
        <b-card >
          <div class="h4 m-0">{{ eachPost.category }}</div>
          <b-table :hover="true" :striped="true" :bordered="true" :small="true" 
            :fixed="true" responsive="lg" :items="eachPost.values" :fields="allFields">
          </b-table>
        </b-card>
      </b-col>
    </b-row> -->
  </b-card-body>
</template>

<script>
let postsRef = firebase.database().ref('posts');

export default {
  name: 'foodMenu',
  data() {
    return {
      allPosts: [],
      allFields: []
    }
  },
  created() {
    postsRef.on('child_added', snapshot => {
      this.allPosts.push(snapshot.val());
      this.allFields = Object.keys(this.allPosts[0].values[0]);
    });
  }
}
</script>

<style>

</style>



